<template>
  <div class="content">
    <my-header v-bind:title="title"></my-header>

    <el-form class="filter" :model="filter" :inline="true">
      <el-form-item>
        <el-button type="primary" @click="addMessage"></el-button>
      </el-form-item>
    </el-form>

    <my-table :tableData="data" :columns="columns" :loading="loading_"></my-table>

    <!--  -->
    <Pagination
      :pagesize="pagesize"
      :current_page="current_page"
      :pageSizes="pageSizes"
      :total="total"
      @changeCurrentPage="changeCurrentPage"
      @changeSizePage="changeSizePage"
    ></Pagination>

    <div class="form">
      <el-form>
        <el-dialog :title="formTitle" :visible.sync="addFormShow">
          <el-form label-position="left" label-width="80px" :model="form" v-loading="form_loading">
            <el-form-item label="">
              <el-input type="text" v-model="form.title"></el-input>
            </el-form-item>

            <my-select :select.sync="form.type" label="" :prop="select" :options="options"></my-select>

            <el-form-item label="">
              <my-date :date.sync="form.send_time" :placeholder="placeholder"></my-date>
            </el-form-item>

            <el-form-item label="">
              <el-input type="textarea" v-model="form.content"></el-input>
            </el-form-item>

            <el-form-item>
              <!-- <el-button type="primary" v-if="true" @click="updateOrCreate(2)"></el-button> -->
              <el-button type="primary" @click="updateOrCreate(null)"></el-button>
              <el-button @click="addFormShow = !addFormShow"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </el-form>
    </div>
  </div>
</template>

<script>
import myHeader from "@/components/admin/common/Header";
import { jsonRemove } from "@/components/unitls";
import myTag from "@/components/From/Tag";
import Button from "@/components/From/Button";
import MySelect from "@/components/admin/common/Select";
import MyDate from "@/components/admin/common/Date";
import MyDropDown from "@/components/From/MyDropDown";
import MyTable from "@/components/From/Table";
import Pagination from "@/components/From/Pagination";
import {page} from "@/mixins/page";

export default {
  name: "Message",
  mixins: [page],
  data() {
    return {
      title: "",
      data: [],
      loading_: false,
      select: "",
      // 
      filter: {},
      // 
      placeholder: "",
      formTitle: "",
      form: {
          type: ""
      },
      form_loading: true, // 
      addFormShow: false, // 
      columns: [
        { prop: "id", label: "ID" },
        { prop: "content", label: "" },
        { prop: "title", label: "" },
        {
          prop: "type",
          label: "",
          render: (h, param) => {
            return h(myTag, {
              props: { effect: "dark", text: param.row.type_ }
            });
          }
        },
        { prop: "create_time", label: "" },
        { prop: "send_time", label: "" },
        {
          prop: "status_",
          label: "",
          render: (h, param) => {
            return h(myTag, {
              props: { effect: "dark", text: param.row.status_ }
            });
          }
        },
        {
          prop: "",
          label: "",
          fixed: "right",
          width: "200",
          render: (h, param) => {
            let buttons = [];
            if (parseInt(param.row.status) == 0) {
              buttons = [
                {
                  func: "send",
                  id: param.row.id,
                  text: "",
                  size: "small"
                },
                {
                  func: "with_draw",
                  id: param.row.id,
                  text: "",
                  size: "small"
                },
                {
                  func: "deleted",
                  id: param.row.id,
                  text: "",
                  size: "small"
                }
              ];
            } else if (parseInt(param.row.status) == 1) {
              buttons = [
                {
                  func: "deleted",
                  id: param.row.id,
                  text: "",
                  size: "small"
                }
              ];
            } else if (parseInt(param.row.status) == 2) {
              buttons = [
                {
                  func: "send",
                  id: param.row.id,
                  text: "",
                  size: "small"
                },
                {
                  func: "deleted",
                  id: param.row.id,
                  text: "",
                  size: "small"
                }
              ];
            }
            return h(Button, {
              props: { buttons: buttons },
              on: {
                send: this.send,
                with_draw: this.with_draw,
                deleted: this.deleted
              }
            });
          }
        }
      ],

      // 
      options: [
        { label: "", value: "" },
        { label: "", value: "" }
      ]
    };
  },

  mounted() {
    this.getList();
  },

  components: {
    myHeader,
    MyTable,
    Button,
    Pagination,
    MySelect,
    MyDate
  },

  methods: {
    // 
    getList() {
      this.loading = true;
      this.$get("/api/message", {
        page: this.current_page,
        pagesize: this.pagesize
      }).then(r => {
        if (r.code == 200) {
          this.setData(r.data.list);
          this.setPage(r.data)
        }
      });
    },

    // 
    setData(data) {
      this.data = data.map((item, key) => {
        item.status_ = this.getStatus_(item.status);
        item.type_ = this.getType_(item.type);

        return item;
      });
      this.loading = false;
    },

    // 
    send(id) {
      this.loading = true;

      this.$post("/api/message/send/" + id).then(r => {
        if (r.code == 200) {
          this.$success_(r.msg);
          this.getList();
        } else {
          this.$error_(r.msg);
        }

        this.loading = false;
      });
    },

    // 
    deleted(id) {
      this.loading = true;

      this.$deletes("/api/message/" + id).then(r => {
        if (r.code == 200) {
          this.$success_(r.msg);
          this.getList();
        } else {
          this.$error_(r.msg);
        }
        this.data = jsonRemove(this.data, "id", id);
        this.loading = false;
      });
    },

    // 
    with_draw(id) {
      this.$post("/api/message/with_draw/" + id).then(r => {
        if (r.code == 200) {
          this.$success_(r.msg);
          this.getList();
        } else {
          this.$error_(r.msg);
        }
      });
    },

    getStatus_(status) {
      switch (parseInt(status)) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
      }
    },

    getType_(status) {
      switch (parseInt(status)) {
        case 0:
          return "";
          break;
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
      }
    },

    // 
    addMessage() {
      this.addFormShow = true;
      this.form_loading = false;
    },

    //
    updateOrCreate() {
      this.form_loading = false;
      this.addFormShow = true;
      this.postData();

      console.log(this.form);
    },

    // 
    postData() {
      this.form.type = this.form.type == "" ? 1 : 2;
      this.loading_ = true;
      this.$post("/api/message", this.form)
        .then(r => this.msg(r))
        .catch(r => {});
    },

    // msg
    msg(r, title = "") {
      if (r.code == 200) {
        this.$success_(title);
        this.getList();
        this.loading_ = false;

        this.form = {}
        this.addFormShow = false;
      } else {
        this.$error_(r.msg);
      }
    }
  }
};
</script>

<style lang="less">
.filter {
  padding-left: 30px;
  div {
    float: left;
  }
}
</style>





