<template>
  <div>
    <my-header v-bind:title="title"></my-header>

    <div class="filter-form">
      <el-row>
        <el-col :span="10">
          <el-form ref="filter" label-position="left">
            <el-form-item label=":" label-width="80px">
              <el-col :span="12">
                <el-input v-model="search_superstore_name"></el-input>
              </el-col>
              <el-col class="line" :span="1"></el-col>
              <el-col :span="5">
                <el-button type="primary" @click="summit_search"></el-button>
              </el-col>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="10">
          <span>
            :
            <font class="font-title">{{ superstore_total }}</font> |
          </span>
          <span>
            :
            <font class="font-title">{{ store_total }}</font> |
          </span>
          <span>
            :
            <font class="font-title">{{ discount_total }}</font> |
          </span>
          <span>
            :
            <font class="font-title">{{ today_discount }}</font> |
          </span>
          <span>
            :
            <font class="font-title">{{ yesterdat_discount }}</font> |
          </span>
        </el-col>
      </el-row>
      <el-row>
        <el-col class="filter-tool" :span="10">
          <el-form>
            <el-button type="primary" @click="add_superstore"></el-button>
            <el-button @click="filter_lower"></el-button>
            <el-button @click="filter_online"></el-button>
            <!--  -->
            <el-button type="primary" @click="filter_delete"></el-button>
          </el-form>
        </el-col>
      </el-row>
    </div>

    <my-table :tableData="list" :columns="columns" :loading="table_loading"></my-table>

    <!--  -->
    <Pagination
      :pagesize="pagesize"
      :current_page="current_page"
      :pageSizes="pageSizes"
      :total="total"
      @changeCurrentPage="changeCurrentPage"
      @changeSizePage="changeSizePage"
    ></Pagination>

    <!--  -->
    <div class="message">
      <el-dialog title="" :visible.sync="simpleVisible" width="30%">
        <div v-loading="simple_loading">
          <ul>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.superstore_name">{{simpleData.superstore_name}}</span>
            </li>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.type_">{{simpleData.type_}}</span>
            </li>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.popularity">{{simpleData.popularity}}</span>
            </li>
            <li>
              <span class="sueper_name">:</span>
              <span
                class="param"
                :title="simpleData.longitude"
              >{{simpleData.longitude}} , {{simpleData.latitude}}</span>
            </li>
            <li @mouseenter="show_(2,$event)" @mouseover="hidden_(2,$event)">
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.do_business">{{simpleData.do_business}}</span>
            </li>
            <li>
              <span class="sueper_name">:</span>
              <span class="param" :title="simpleData.address">{{simpleData.address}}</span>
            </li>
          </ul>
        </div>
      </el-dialog>
    </div>

    <!--  -->
    <el-dialog :title="inputSuperStoreTitle" :visible.sync="inputSuperStore">
      <el-form
        :model="superstore_form"
        ref="superstore_form"
        v-loading="edit_loadding"
        label-width="80px"
        :rules="rules"
      >
        <el-form-item label="" prop="superstore_name">
          <el-input v-model="superstore_form.superstore_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-col :span="26">
            <el-select v-model="superstore_form.type" placeholder="">
              <el-option
                v-for="(item, index) in types"
                :key="index"
                :value="item.key"
                :label="item.value"
              ></el-option>
            </el-select>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="type">
          <el-col :span="26">
            <el-select v-model="superstore_form.status" placeholder="">
              <el-option value="" label=""></el-option>
              <el-option value="" label=""></el-option>
            </el-select>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="popularity">
          <el-input v-model="superstore_form.popularity"></el-input>
        </el-form-item>
        <el-form-item label="" :inline="true" prop="longitude_latitude">
          <el-col :span="8">
            <el-input v-model="superstore_form.longitude"></el-input>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="8">
            <el-input v-model="superstore_form.latitude"></el-input>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="3">
            <el-button type="primary" @click="onAddress"></el-button>
          </el-col>
        </el-form-item>

        <el-form-item label="" prop="do_business">
          <el-input v-model="superstore_form.do_business"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-input v-model="superstore_form.address"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cacheEdit = false"> </el-button>
        <el-button
          type="primary"
          v-if="inputSuperStoreTitle == ''"
          @click="confirmUpdate('superstore_form')"
        ></el-button>
        <el-button
          type="primary"
          v-if="inputSuperStoreTitle == ''"
          @click="confirmCreate('superstore_form')"
        ></el-button>
      </div>
    </el-dialog>

    <el-dialog title="" :visible.sync="amap" class="amap-body">
      <div class="dialog-content amap-content">
        <el-amap vid="amapDemo" :center="center" :zoom="zoom" class="amap-demo" :events="events"></el-amap>
        <span style="color: red;">
          {{superstore_form.longitude}} - {{superstore_form.latitude}}
          <br>
          {{superstore_form.address}}
        </span>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="amap = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import myHeader from "./common/Header";
import myTag from "@/components/From/Tag";
import MyDropDown from "@/components/From/MyDropDown";
import MyTable from "@/components/From/Table";
import Pagination from "@/components/From/Pagination";
import { page } from "@/mixins/page";

export default {
  name: "SuperStore",
  mounted() {
    this.getList();
    this.getDataInfo();
  },
  components: {
    myHeader,
    myTag,
    MyTable,
    MyDropDown,
    Pagination
  },
  mixins: [page],
  data() {
    var self = this;
    return {
      id: 0,
      title: "",
      inputSuperStoreTitle: "",
      table_loading: true,
      simple_loading: true,
      edit_loadding: true,
      list: [],
      simpleData: {},
      simpleVisible: false,
      inputSuperStore: false,
      amap: false,
      visible: false,
      superstore_form: {},
      superstore_total: 0,
      store_total: 0,
      discount_total: 0,
      today_discount: 0,
      multipleSelection: "",
      multipleSelectionIds: [],
      yesterdat_discount: 0,
      // 
      search_superstore_name: "",
      columns: [
        { prop: "id", label: "ID", width: "55" },
        { prop: "superstore_name", label: "" },
        {
          prop: "type_",
          label: "",
          render: (h, params) => {
            return h(myTag, {
              props: {
                size: "medium",
                text: params.row.type_
              }
            });
          }
        },
        { prop: "store_num", label: "", width: "105" },
        { prop: "discount_num", label: "", width: "135" },
        { prop: "remark", label: "" },
        {
          prop: "",
          fiexed: "right",
          label: "",
          render: (h, param) => {
            let dropDownData = {
              label: ""
            };
            let buttons = [
              { label: "", func: { func: "showDetail", id: param.row.id } },
              { label: "", func: { func: "edit", id: param.row.id } },
              {
                label: "",
                func: { func: "go_store", id: param.row.id }
              }
            ];

            if (param.row.status == 1) {
              dropDownData.items = [
                { label: "", func: { func: "lower", id: param.row.id } },
                { label: "", func: { func: "delete", id: param.row.id } }
              ];
            } else {
              dropDownData.items = [
                { label: "", func: { func: "online", id: param.row.id } },
                { label: "", func: { func: "delete", id: param.row.id } }
              ];
            }

            // MyDropDownupdatedel
            return h(MyDropDown, {
              props: { dropDownData: dropDownData, buttons: buttons },
              on: {
                lower: this.lower,
                delete: this.delete,
                online: this.online,
                edit: this.edit,
                go_store: this.go_store,
                showDetail: this.showDetail
              }
            });
          }
        }
      ],
      //   0 1 2
      types: [
        {
          key: 0,
          value: ""
        },
        {
          key: 1,
          value: ""
        },
        {
          key: 2,
          value: ""
        }
      ],
      rules: {
        superstore_name: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            mix: 2,
            max: 100,
            message: "2-100",
            trigger: "blur"
          }
        ],
        popularity: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        do_business: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      zoom: 12,
      center: [114.12593, 22.649967],
      events: {
        click(e) {
          let { lng, lat } = e.lnglat;
          self.superstore_form.longitude = lng;
          self.superstore_form.latitude = lat;

          //  SDK 
          var geocoder = new AMap.Geocoder({
            radius: 1000,
            extensions: "all"
          });

          var auto = new AMap.Autocomplete({
            input: "amap_search"
          });

          geocoder.getAddress([lng, lat], function(status, result) {
            if (status === "complete" && result.info === "OK") {
              if (result && result.regeocode) {
                self.superstore_form.address =
                  result.regeocode.formattedAddress;
                self.$nextTick();
              }
            }
          });
        }
      },
      lng: 0,
      lat: 0
    };
  },
  methods: {
    // 
    edit(id) {
      this.inputSuperStoreTitle = "";
      this.inputSuperStore = true;
      this.getSimple(id);
      this.edit_loadding = false;
    },
    // 
    getList() {
      this.$get("/api/superstore", {
        page: this.current_page,
        pagesize: this.pagesize
      }).then(response => {
        this.list = response.data.list;
        this.setPage(response.data);
        this.table_loading = false;
      });
    },
    // 
    showDetail(id) {
      this.getSimple(id);
      this.simpleVisible = true;
      this.simple_loading = false;
    },
    // 
    getSimple(id) {
      this.id = id;
      this.$get(`/api/superstore/${id}`).then(response => {
        if (response.code == 200) {
          this.simpleData = response.data;
          this.superstore_form = response.data;
          if (this.superstore_form.status == 1) {
            this.superstore_form.status = "";
          } else {
            this.superstore_form.status = "";
          }
        }
      });
    },
    show_(index, e) {
      console.log(e.target);
    },
    hidden_(index, e) {
      console.log(e);
    },
    // 
    cacheEdit() {},

    updateData(data) {
      var postData = {
        superstore_name: data.superstore_name,
        longitude: data.longitude,
        latitude: data.latitude,
        type: data.type,
        popularity: data.popularity,
        do_business: data.do_business,
        address: data.address,
        status: data.status == "" ? 1 : 2
      };

      this.$put(`/api/superstore/` + this.id, postData).then(response => {
        if (response.code == 200) {
          this.edit_loadding = false;
          this.$success_("");
          this.getList();
        } else {
          this.$error_(response.msg);
        }
      });
    },

    // 
    onAddress() {
      this.amap = true;
      if (this.superstore_form.longitude) {
        this.center = [
          this.superstore_form.longitude,
          this.superstore_form.latitude
        ];
      }
    },

    // 
    lower(id) {
      this.$post(`/api/superstore/lower/${id}`).then(r => {
        if (r.code == 200) {
          this.$success_("");
          this.getList();
        } else {
          this.$error_(r.msg);
        }
      });
    },

    // 
    online(id) {
      console.log(id);
      this.$post(`/api/superstore/online/${id}`).then(r => {
        if (r.code == 200) {
          this.$success_("");
          this.getList();
        } else {
          this.$error_(r.msg);
        }
      });
    },

    // 
    delete(id) {
      this.$confirm(",", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$deletes(`/api/superstore/${id}`).then(r => {
          if (r.code == 200) {
            this.$success_("");
            this.getList();
          } else {
            this.$error_(r.msg);
          }
        });
      });
    },

    tableRowClassName({ row }) {
      if (row.status == 2) {
        row.remark = "- ";
        return "color-lower";
      }
    },

    // 
    // 
    summit_search() {
      // this.$post('')
      this.$success_("");
    },

    // 
    add_superstore() {
      this.superstore_form = {};
      this.inputSuperStore = true;
      this.inputSuperStoreTitle = "";
      this.edit_loadding = false;

      this.superstore_form.type = 1;
      this.superstore_form.status = 1;
      this.superstore_form.popularity = 5;
    },

    // 
    confirmCreate(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          console.log(this.superstore_form);
          this.edit_loadding = true;
          this.post_superstore(this.superstore_form);
        } else {
          return false;
        }
      });
    },

    // 
    confirmUpdate(form) {
      this.$refs[form].validate(valid => {
        if (valid) {
          this.edit_loadding = true;
          this.updateData(this.superstore_form);
        } else {
          return false;
        }
      });
    },

    // 
    post_superstore(data) {
      var postData = {
        superstore_name: data.superstore_name,
        longitude: data.longitude,
        latitude: data.latitude,
        type: data.type,
        popularity: data.popularity,
        do_business: data.do_business,
        address: data.address,
        status: data.status
      };

      let that = this;

      this.$post("/api/superstore", postData).then(function(response) {
        if (response.code == 200) {
          that.$success_("");
          that.getList();
          that.edit_loadding = false;
          that.inputSuperStore = false;
        } else {
          that.edit_loadding = false;
          that.$error_(response.msg);
        }
      });
    },

    // 
    filter_online() {
      this.getSelected();
      let ids = this.multipleSelectionIds.join(",");
      this.$post(`/api/superstore/batch_online?ids=${ids}`).then(r => {
        if (r.code == 200) {
          this.$success_("");
          this.getList();
        } else {
          this.$error_(r.msg);
        }
      });
    },

    // 
    filter_lower() {
      this.getSelected();
      let ids = this.multipleSelectionIds.join(",");
      this.$post(`/api/superstore/batch_lower?ids=${ids}`).then(r => {
        if (r.code == 200) {
          this.$success_("");
          this.getList();
        } else {
          this.$error_(r.msg);
        }
      });
    },

    // 
    filter_delete() {
      this.$error_("");
    },

    // 
    go_store(id) {
      localStorage.setItem("superstore_id", id);
      this.$router.push({ path: "/admin/store" });
    },

    // 
    getDataInfo() {
      this.$get("/api/gather").then(r => {
        if (r.code == 200) {
          this.superstore_total = r.data.superstore_total;
          this.store_total = r.data.store_total;
          this.discount_total = r.data.discount_total;
          this.today_discount = r.data.today_discount;
          this.yesterdat_discount = r.data.yesterdat_discount;
        }
      });
    },

    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.getSelected();
    },

    // 
    getSelected() {
      var data = this.multipleSelection;
      var ids = [];
      data.forEach(row => {
        ids.push(row.id);
      });
      this.multipleSelectionIds = ids;
    }
  }
};
</script>

<style lang="less">
.pagination {
  margin-top: 20px;
  .page {
    float: right;
    margin-right: 10%;
  }
}

.message {
  ul,
  li {
    padding: 0;
    margin: 0;
    list-style: none;
    color: black;
    font-weight: bold;
  }

  li {
    display: block;
    height: auto;
    overflow: hidden;
    margin-top: 15px;
  }

  .sueper_name,
  .param {
    display: block;
    float: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .sueper_name {
    width: 30%;
    text-align: right;
    padding-right: 20px;
    color: #808080;
  }

  .param {
    width: 50%;
    text-align: left;
  }
}

.amap-content {
  height: 500px;
  width: 100%;
}

.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}

.color-lower {
  color: #d0d0d0;
}

.filter-form {
  padding-left: 30px;

  .filter-tool {
    margin-left: 0;
    .el-button {
      float: left;
    }
  }
}
.font-title {
  color: red;
}
</style>



